import BaseComponent from '../base';
import DomHelper from '../helper';

const Card = (() => {
    const componentName = 'ui-card';
    let init = (node) => new CardComponent(node);
    DomHelper.init(componentName, init);
})()

class CardComponent extends BaseComponent {
    componentName = 'card';
    name = 'Card Component';
    modVisible = 'visible';
    TIMER_KEY = 'data-timer';
    DELAY_KEY = 'data-delay';
    DEFAULT_TIMER = 3000;
    DEFAULT_DELAY = 0;

    constructor(element) {
        super({});
        this.element = element;
        this.setTimer(parseInt(element.getAttribute(this.TIMER_KEY)) || null);
        this.setDelay(parseInt(element.getAttribute(this.DELAY_KEY)) || null);
        this.index = 0;
        this.actions = {
            show: this.define() + '--' + this.modVisible
        };
        this.start();
    }

    setTimer(timer) {
        this.timer = timer || this.DEFAULT_TIMER;
    }

    setDelay(delay) {
        this.delay = delay || this.DEFAULT_DELAY;
    }

    start() {
        let content = this.select('card__content', this.element);
        this.contentGroups = this.select('card__group', content[0]);
        this.shift(0);

        setTimeout(() => {
            this.action();
        }, this.delay);
    }

    reset(current) {
        for (let index = 0; index < this.contentGroups.length; index++) {
            let group = this.contentGroups[index];
            if (group !== current) {
                group.classList.remove(this.actions.show);
            }
        }
    }

    shift(index) {
        let current = this.contentGroups[index];
        current.classList.add(this.actions.show);
        this.reset(current);
    }

    action() {
        this.interval = setInterval(() => {
            this.index++;
            if (this.index == this.contentGroups.length) {
                this.index = 0;
            }
            this.shift(this.index);
        }, this.timer);
    }

    describe() {
        return this.name;
    }
}


